body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
#nav-bar {
	display: -ms-flexbox;
	display: flex;
	height: 30px;
    padding: 8px;
}
.nav-background {
	background-color: #232f3e;
}
.nav-left {
	-ms-flex: 2 1;
	    flex: 2 1;
}
.nav-right {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
}
.menu-btn {
	color: white;
	padding: 8px;
	-ms-flex-line-pack: center;
	    align-content: center; 
}
.logo {
	color: #12dbed;
	padding: 8px;
}
.column {
  float: left;
  width: 33.333%;
  padding: 5px;
}

/* Clear floats after image containers */
.row::after {
  content: "";
  clear: both;
  display: table;
}
.fade {
            position: relative;
            width: 100%;
        }

        .image {
          opacity: 1;
          display: block;
          width: 100%;
          height: auto;
          -webkit-transition: .5s ease;
          -o-transition: .5s ease;
          transition: .5s ease;
          -webkit-backface-visibility: hidden;
                  backface-visibility: hidden;
        }

        .middle {
          -webkit-transition: .5s ease;
          -o-transition: .5s ease;
          transition: .5s ease;
          opacity: 0;
          position: absolute;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%)
        }

        .fade:hover .image {
          opacity: 0.3;
        }

        .fade:hover .middle {
          opacity: 1;
        }

        .text {
          background-color: #4CAF50;
          color: white;
          font-size: 16px;
          padding: 16px 16px;
        }
.parallax{
    background-image: url("./logoSmall.jpg");
    height: 500px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
}


